//layer,feature,

import Feature, { FeatureLike } from "ol/Feature";
import Point from "ol/geom/Point";
import VectorSource from "ol/source/Vector";
import { Icon, Style } from "ol/style";
import { Tile as TileLayer, Vector as VectorLayer } from "ol/layer";
import { unByKey } from "ol/Observable";
import Map from "ol/Map";
import { EventsKey } from "ol/events";
import { transform, toLonLat } from "ol/proj";

export interface FeatureObj {
  id: string;
  pngIcon: string;
  coordinate: number[];
  info: object;
}
declare enum IconAnchorUnits {
  FRACTION = "fraction",
  PIXELS = "pixels",
}

//
export function createFeature(feature: FeatureObj): Feature {
  const iconFeature = new Feature({
    geometry: new Point(feature.coordinate),
    custom: feature, //featurecustom
  });
  const iconStyle = new Style({
    image: new Icon({
      anchor: [0.5, 46],
      anchorXUnits: IconAnchorUnits.FRACTION,
      anchorYUnits: IconAnchorUnits.PIXELS,
      src: feature.pngIcon,
      scale: 0.8, //,
    }),
  });
  iconFeature.setStyle(iconStyle);
  iconFeature.setId(feature.id);
  return iconFeature;
}

//
export function createVecLayer(featureList: Feature[], id = ""): VectorLayer {
  const vectorLayer = new VectorLayer({
    source: new VectorSource({ features: featureList }),
  });
  vectorLayer.set("id", id);
  return vectorLayer;
}

interface HandlerFun {
  (feature: FeatureLike): void;
}

//
export function initFeatureClick(map: Map, callback: HandlerFun): EventsKey {
  const handler = map.on("click", async function(evt) {
    const feature = map.forEachFeatureAtPixel(evt.pixel, function(feature) {
      return feature;
    });
    if (feature) {
      callback(feature);
    }
  });
  return handler;
}

//hover
export function initFeatureHover(map: Map, callback: HandlerFun): EventsKey {
  const handler = map.on("pointermove", async function(evt) {
    const feature = map.forEachFeatureAtPixel(evt.pixel, function(feature) {
      return feature;
    });
    if (feature) {
      callback(feature);
    }
  });
  return handler;
}

//
export function destroyHandler(eventHandler: EventsKey): void {
  if (eventHandler != null) {
    unByKey(eventHandler);
  }
}

//
export function getCoordinate(map: Map): EventsKey {
  const handler = map.on("singleclick", function(e) {
    const coordinate = e.coordinate;
    // var point = transform(coordinate, "EPSG:3857", "EPSG:4326");
    const point = toLonLat(coordinate, "EPSG:3857");
    console.log(":", coordinate);
    console.log(":", point);
  });
  return handler;
}
